<template>
  <div class="goods-item" @click="godetail">
    <img :src="showImg" alt=""  @load="imageLoad"/>
    <div class="goods-info">
      <p>{{goodsItem.title}}</p>
      <span class="price">{{goodsItem.price}}</span>
      <span class="collect">{{goodsItem.cfav}}</span>

    </div>
    
  </div>
</template>

<script>
  export default {
    name: "GoodListItem",
    props:{
      goodsItem:{
        type:Object,
        default(){
          return {}
        }
      }
    },
    computed:{
      showImg(){
        return this.goodsItem.img || this.goodsItem.image || this.goodsItem.show.img
        // this.goods.img || this.goods.image || this.goods.show.img
      }
    },
    methods:{
      imageLoad(){
        this.$bus.$emit('itemImageLoad')

        // if(this.$router.path.indexOf('/home')){
        //   this.$bus.$emit('itemImageLoad')
        // }else  if(this.$route.path.indexOf('/detail')){
        //   this.$bus.$emit('detailItemImgLoad')
        // }

      },
      godetail(){
        this.$router.push({
          path:'/detail/'+this.goodsItem.iid,
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  .goods-item{
    width: 47%;
    padding-bottom: 40px;
    position: relative;
    img{
      width: 100%;
      border-radius: 5px;
    }
    .goods-info{
      font-size: 12px;
      position: absolute;
      bottom: 5px;
      left: 0;
      right: 0;
      overflow: hidden;
      text-align: center;
      p{
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin-bottom: 3px;
      }
      .price{
        color: #ff5777;
        margin-right: 20px;
      }
      .collect{
        position: relative;
        &:before{
          content: '';
          position: absolute;
          left: -15px;
          top: -1px;
          width: 14px;
          height: 14px;
          background: url("../../../assets/images/home/collect.svg") 0 0/14px 14px;
        }
      }

    }
  }
</style>