<template>
  <div id="detail" >
    <detailtopbar class="topbar" @titleClick="titleClicks" ref="detailnavbar"/>
    <Scroll class="scroll" ref="scroll" @scroll="contentScroll" :probe-type="3">
      <detail-swipter :swipterdata="swipterimg"/>
      <Detail-base-info :goods="goods"/>
      <Detail-shop-info :shop="shop"/>
      <Detail-goods-info :detailInfo="detailInfo" @imageLoad="imageload"/>
      <Detail-param-info :paramInfo="paramInfo" ref="param"/>
      <Detail-comment-info :commentInfo="commentInfo" ref="comment"/>
      <Good-list :goods="recommends" ref="recommend"/>
    </Scroll>
    <Detail-bottom-bar @addToCart="addCart"/>
    <Toast :message="message" :show="show"/>
  </div>
</template>

<script>
  import  {getDetail,Goods,Shop,GoodsParam,getRecommend} from '../../NetWork/detail'
  import detailtopbar from "./childrende/detailtopbar";
  import detailSwipter from "./childrende/detailSwipter";
  import DetailBaseInfo from "./childrende/DetailBaseInfo";
  import DetailShopInfo from "./childrende/DetailShopInfo";
  import DetailGoodsInfo from "./childrende/DetailGoodsInfo";
  import Scroll from "../../components/public/scroll/Scroll";
  import DetailParamInfo from "./childrende/DetailParamInfo";
  import DetailCommentInfo from "./childrende/DetailCommentInfo";
  import GoodList from "../../components/public/Goods/GoodList";
  import DetailBottomBar from "./childrende/DetailBottomBar";
  import {debounce} from "../../components/public/utils/utils";
  import {backtopmixin} from '../../commion/mixin'
  import {mapActions} from 'vuex'
  import Toast from "../../components/public/toast/Toast";
  export default {
    name: "detail",
    mixins:[backtopmixin],
    components:{
      detailtopbar,
      detailSwipter,
      DetailBaseInfo,
      DetailShopInfo,
      DetailGoodsInfo,
      DetailParamInfo,
      DetailCommentInfo,
      DetailBottomBar,
      GoodList,
      Toast,
      Scroll
    },
    data(){
      return {
        iid:null,
        swipterimg:null,
        goods:{},
        shop:{},
        detailInfo:{},
        paramInfo:{},
        commentInfo:{},
        recommends:[],
        itemimgdata:null,
        themtopY:[],
        getThemeTopy:null,
        message:'',
        show:false,
      }
    },
    created() {
      this.iid = this.$route.params.iid;
      getDetail(this.iid).then((res,req)=>{
        // console.log(res)
        let detaildatate = res.result;
        let data = detaildatate.itemInfo.topImages;
        this.swipterimg = data;
        this.goods = new Goods(detaildatate.itemInfo,detaildatate.columns,detaildatate.shopInfo.services)
        this.shop = new Shop(detaildatate.shopInfo)
        this.detailInfo = detaildatate.detailInfo
        this.paramInfo = new GoodsParam(detaildatate.itemParams.info,detaildatate.itemParams.rule);
        if(detaildatate.rate.cRate !=0){
          this.commentInfo = detaildatate.rate.list[0]
        }



      })
      getRecommend().then((res,req)=>{
        this.recommends = res.data.list;
      })
    },
    methods:{
      // ...mapActions([addCart]),
      imageload(){
        this.$refs.scroll.refresh();
        this.themtopY = [];
        this.themtopY.push(0);
        this.themtopY.push(this.$refs.param.$el.offsetTop-50);
        this.themtopY.push(this.$refs.comment.$el.offsetTop-50);
        this.themtopY.push(this.$refs.recommend.$el.offsetTop-50);
        // console.log( this.themtopY)
      },
      titleClicks(index){
        console.log(index)
        this.$refs.scroll.scrollTo(0,-this.themtopY[index],100)
      },
      contentScroll(position){
        let positionY = -position.y;
        let length = this.themtopY.length
        for (let i = 0; i < length ; i++) {
          if(this.currentIndex !== i && ((i<length-1 && positionY >= this.themtopY[i] && positionY < this.themtopY[i+1])||
              (i === length -1 &&positionY >= this.themtopY[i]))){
            this.currentIndex = i;
            this.$refs.detailnavbar.current  = this.currentIndex
          }
        }
      },
      addCart(){
        // console.log(123)
        //获取购物车需要展示的信息
        const product= { }
        product.image = this.swipterimg[0];
        product.title = this.goods.title;
        product.desc =  this.goods.desc;
        product.newpress = this.goods.newPrice;
        product.realPrice = this.realPrice;
        product.iid = this.iid;
        //将商品添加到购物车
        //   this.$store.commit('addCart',product)
        // this.addCart(product).then(res=>{
        //   console.log(res)
        // })
          this.$store.dispatch('addCart',product).then(res=>{
            console.log(res)
            this.$toast.show(res,2000)
        })
      },
    },
    mounted() {
      let refresh = debounce(  this.$refs.scroll.refresh,500);
      this.itemimgdata = ()=>{
        refresh()
      };
      this.$bus.$on('itemImageLoad',this.itemimgdata);

    },
    destroyed() {
      this.$bus.$off('itemImageLoad',this.itemimgdata)
    },
    updated() {

    }
  }
</script>

<style scoped>
  #detail{
    position: relative;
    z-index: 9;
    background: white;
    height: 100vh;
  }
  .topbar{
    position: relative;
    z-index: 9;
    background: white;
  }
  .scroll{
    height: calc(100% - 44px - 50px);
  }

</style>