<template>
  <div>
    <Nav-bar>
      <div slot="center" class="nav-center">
        <div
        v-for="(item,index) in txt"
        class="nav-item"
        :class="{actvie:index===current}"
        @click="handless(index)">
          {{item}}
        </div>
      </div>
      <div slot="left" class="left">
        <img src='../../../assets/images/detail/back.svg' alt="" @click="backhome">
      </div>
    </Nav-bar>
  </div>
</template>

<script>
  import NavBar from "../../../components/public/TopBar/NavBar";
  export default {
    name: "detailtopbar",
    components:{
      NavBar
    },
    data(){
     return{
       txt:['商品','参数','评论','推荐'],
       current:0
     }
    },
    methods:{
      handless(index){     //改变topnav的颜色
        this.current = index
        this.$emit('titleClick',index)
      },
      backhome(){            //回到上一级
        this.$router.back()
      }
    }
  }
</script>

<style scoped lang="scss">
  .nav-center{
    display: flex;
    .nav-item{
      flex: 1;
      font-size: 13px;
    }
  }
  .left{
    img{
      margin-top: 10px;
    }
  }
  /*活动颜色*/
  .actvie{
    color: #ff8198;
  }
</style>